.video {

}

.video__wrap {
  padding-top: 120px;
  padding-bottom: 460px;
  background-color: $video-background;

  @media (max-width: $tablet-width-only) {
    padding-top: 70px;
    padding-bottom: 217px;
  }

  @media (max-width: $mobile-width-only) {
    padding-top: 84px;
    padding-bottom: 146px;
  }
}

.video__header {
  margin-top: 0;
  margin-bottom: 20px;
  padding-right: 8px;
  text-align: center;
  font-weight: 600;
  font-size: 30px;
  line-height: 30px;
  letter-spacing: -0.005em;
  color: $white;

  @media (max-width: $tablet-width-only) {
    padding-right: 0;
    font-size: 24px;
  }

  @media (max-width: $mobile-width-only) {
    padding-right: 12px;
  }
}

.video__text {
  margin-top: 0;
  margin-bottom: 20px;
  text-align: center;
  font-weight: 300;
  font-size: 16px;
  line-height: 28px;
  color: $white;

  @media (max-width: $mobile-width-only) {
    margin-left: 16px;
    margin-right: 16px;
  }
}

.video__br {
  @media (max-width: $tablet-width-only) {
    display: none;
  }

  &--tablet {
    display: none;

    @media (max-width: $tablet-width-only) {
      display: inline;
    }

    @media (max-width: $mobile-width-only) {
      display: none;
    }
  }
}

.video__player {
  position: relative;
  margin-top: -424px;
  margin-left: 17.8%;
  width: 929px;

  @media (max-width: $tablet-width-only) {
    margin-top: -196px;
    margin-left: 17.3%;
    width: 503px;
  }

  @media (max-width: $mobile-width-only) {
    margin-top: -111px;
    margin-left: 15px;
    margin-right: 18px;
    width: auto;
  }
}

.video__btn {
  position: absolute;
  top: 229px;
  left: 50%;
  transform: translateX(-50%);
  background: transparent;
  border: none;

  @media (max-width: $tablet-width-only) {
    top: 108px;
  }

  @media (max-width: $mobile-width-only) {
    top: 45px;
  }

  &:hover {
    opacity: 0.9;
    cursor: pointer;
  }

  &:active {
    opacity: 0.7;
  }
}
